                  color: #9747ff;
                  text-align: center;
                  font-family: "Inter", sans-serif;
                  font-size: 11.738px;
                  font-style: normal;
                  font-weight: 500;
                  line-height: normal;
                  display: flex;
                  text-align: center;
                  width: fit-content;
