                      visibility: visible;
                      opacity: 1;
                    }
                  }

                  .section-requirement-icon {
                    display: flex;
                    position: relative;
                    z-index: 99;
                  }

                  .section-requirement-detail {
                    font-size: 12px;
                    width: 240px;
                    background-color: #ffffff;
                    text-align: left;
                    border-radius: 6px;
                    padding: 15px 18px;
                    position: absolute;
                    z-index: 1;
                    bottom: 100%;
                    right: -18px;
                    margin-top: 4px;
                    line-height: 1.4em;
                    visibility: hidden;
                    border: 1px solid #e3e4e6;
                    margin-bottom: 10px;
                    font-size: 12px;
                    opacity: 0;

                    &:after,
                    &:before {
                      position: absolute;
                      display: block;
                      content: "";
                      width: 0;
                      height: 0;
                      border-style: solid;
                      border-width: 7px;
                      border-color: #fff transparent transparent transparent;
                      right: 17px;
                      bottom: -14px;
                    }

                    &:before {
                      border-color: #e3e4e6 transparent transparent transparent;
                      border-width: 9px;
                      bottom: -18px;
                      right: 15px;
