          .f-demo-paging-button {
            cursor: pointer;
            background: #9747ff;
            border: 1px solid #9747ff;
            border-radius: 4px;
            display: inline-block;
            margin-top: 30px;
            margin-bottom: 50px;
            padding: 15px 35px;
            transition: background 0.3s ease;

            span {
              color: #fff;
              font-family: "Inter", sans-serif;
              font-style: normal;
              font-weight: 600;
              font-size: 14px;
            }

