            }
          }
        }

        .empty-content {
          display: flex;
          height: 100%;
          width: 100%;
          align-items: center;
          justify-content: center;

          h3 {
            font-size: 20px;
            margin-left: 10px;
            color: #d2d2d2;
          }

          svg {
            display: block;
            fill: #d2d2d2;
          }
