      }
    }

    .upgrade-pro-wrapper {
      display: flex;
      flex: 1;
      flex-direction: column;
      width: 100%;
      box-sizing: border-box;

      .upgrade-pro-content {
        display: flex;
        flex-direction: column;
        padding: 50px 40px 69px;
        justify-content: center;
        position: relative;
        box-sizing: border-box;
        height: 694px;
        justify-content: flex-end;
        position: relative;

        .banner {
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
          height: inherit;

          &.center {
            width: 547px;
            height: auto;
            position: relative;
          }

          &.item {
            width: unset;
            height: fit-content;

            &.arrow {
              top: 513.93px;
              left: 753.04px;
              transform: rotate(356deg);
            }
          }

          &.background {
            border-radius: unset;
            border: unset;
          }
        }

        .upgrade-pro-button {
          z-index: 10;
          position: relative;
          margin: 24px auto 0;
          cursor: pointer;
          transition: all 0.5s ease;
          background: radial-gradient(
            112.43% 113.24% at 49.77% 0%,
            #ba70ff 3.52%,
            #2f47ff 100%
          );
          border-radius: 10px;
          overflow: hidden;

          &::before {
            content: "";
            position: absolute;
            inset: 0;
            padding: 2px;
            background: linear-gradient(
              180deg,
              #9760ff 0%,
              rgba(255, 255, 255, 0.1) 100%
            );
            box-shadow: 0px 4px 4px -4px #00000033;
            border-radius: 10px;
            mask:
              linear-gradient(#000 0 0) content-box,
              linear-gradient(#000 0 0);
            mask-composite: exclude;
          }

          .button-content-wrapper {
            color: white;
            display: flex;
            padding: 18px 40px;
            justify-content: center;
            align-items: center;
            font-size: 16px;
            font-weight: 500;
            gap: 6px;
          }

          svg,
          span {
            transition: all 0.5s ease;
          }
        }

        .upgrade-pro-button:hover {
          transform: scale(0.94);

          svg,
          span {
            opacity: 0.75;
          }
        }

        .content-title {
          z-index: 1;
          position: relative;
          font-stretch: normal;
          line-height: 1.1;
          text-align: center;
          color: #fff;
          font-family: "Sora", sans-serif;
          font-weight: 800;
          font-size: 48px;
          vertical-align: middle;
          max-width: 673px;

          .gradient-text {
            background: linear-gradient(
              90.09deg,
              #e948ff 57.47%,
              #466aff 86.98%
            );
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
            position: relative;
          }
        }

        .content-desc {
          z-index: 1;
          max-width: 673px;
          position: relative;
          margin: 12px auto 0;
          max-width: 400px;
          font-family: "Inter", sans-serif;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.4;
          letter-spacing: normal;
          color: #ffffff8f;
          font-weight: 400;
          font-size: 14px;
          text-align: center;
          vertical-align: middle;
        }

        .upgrade-image {
          z-index: 1;
          position: relative;
          width: 640px;
          margin: -10px auto 35px;
        }
      }

      .wizard-actions {
        width: auto;
        margin-top: 0;
      }
    }

    .finalizing {
      display: flex;
      flex: 1;
      flex-direction: column;
      width: 100%;
      box-sizing: border-box;
      height: 771px;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 221px;

      .image-wrapper {
        width: 100%;
        box-sizing: border-box;
        position: relative;

        .complete {
          position: absolute;
          top: 0;
          left: 0;

          &.icon {
            width: 80px;
            height: 80px;
            top: 253px;
            left: 50%;
            transform: translateX(-50%);
          }

          &.background {
            width: 480px;
            height: 480px;
            top: 65px;
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }

      .final-detail {
        max-width: 425px;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 9;

        > * {
          text-align: center;
          font-family: "Inter", sans-serif;
        }

        h3.final-title {
          font-weight: 600;
          font-size: 32px;
          line-height: 100%;
          vertical-align: middle;
        }

        p.final-desc {
          font-weight: 400;
          font-size: 14px;
          line-height: 140%;
          vertical-align: middle;
          margin-top: 12px;
        }

        div.button-visit {
          border-radius: 6px;
          padding: 12px 22px;
          background: linear-gradient(180deg, #af6af9 0%, #9c4dff 100%);
          font-weight: 600;
          font-size: 12px;
          line-height: 18px;
          color: rgba(255, 255, 255, 1);
          cursor: pointer;
          transition: transform 0.3s ease;
          margin-top: 24px;

          &:hover {
            transform: scale(0.94);
          }
        }
      }
    }
  }
}

.wizard-container-body {
  .wizard-item {
    display: flex;
    align-items: center;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 15px 20px;
    cursor: pointer;
    margin-bottom: 20px;

    &.selected {
      border-color: #9c4dff;
    }

    .wizard-item-icon {
      margin-right: 20px;
    }

    .wizard-item-content {
      flex: 1;

      h3 {
        font-weight: 300;
        font-size: 20px;
        line-height: 25px;
        margin: 5px 0 5px;
        color: #011627;

        strong {
          font-weight: bold;
        }
      }

      p {
        color: #40505d;
        font-size: 14px;
        line-height: 140%;
        margin: 10px 0;
      }
    }

    .wizard-item-check {
      padding: 0 10px;
    }

    &.selected {
      .wizard-item-check {
        path {
          fill: #9c4dff;
        }
      }
    }
  }
}

.wizard-container-footer {
  margin: 40px -40px -20px;
  border-top: 1px solid #e3e4e6;

  .wizard-button {
    display: flex;
    justify-content: flex-end;
    gap: 15px;
    padding: 20px 40px;
  }

  .wizard-progress {
    position: relative;

    &.hide {
      opacity: 0;
      visibility: hidden;
      position: absolute;
    }

    .wizard-progress-wrapper {
      display: flex;
      width: 100%;
      justify-content: space-between;
      padding: 0 40px;
      box-sizing: border-box;
      line-height: 65px;
    }

    .progress-text,
    .progress-step {
      font-size: 12px;
      color: #99a2a9;
    }

    .progress-bar {
      height: 10px;
      display: block;
      width: 100%;
      background: #bbbbbb;
      position: relative;

      .progress-done {
        position: absolute;
        height: 100%;
        width: 0%;
        background: linear-gradient(90deg, #9c4dff 0%, #5cd0da 100%);
        transition: width 1s ease-in-out;
      }
    }
  }

  .button {
    background: #9c4dff;
    color: #fff;
    padding: 8px 25px;
    border-radius: 20px;
    font-size: 14px;
    line-height: 16px;
    cursor: pointer;

    &.secondary {
      color: #011627;
      background: transparent;
    }
  }
}

.loading {
  margin: 0 auto;
  display: block;
  width: 28px;

  svg {
    animation: spin 2s infinite linear;
  }

  &.disabled {
    background-color: #99a2a9;
  }

  &.expire {
    background-color: #e4b200;
    color: #ffffff;
  }
}

.essential-importer-body {
  margin: 20px;

  .essential-close-importer {
    position: relative;
    cursor: pointer;

    svg {
      right: 20px;
      position: fixed;
    }
  }

  .essential-finish-body {
    display: grid;
    align-items: center;

    svg {
      margin: 20px auto;
    }

    .title {
      color: var(--primary, #0f1538);
      text-align: center;
      font-family: Inter;
      font-size: 22px;
      font-style: normal;
      font-weight: 600;
      line-height: 1.25em;
    }

    > span {
      color: var(--be-gray-text, #7d8292);
      text-align: center;
      font-family: Inter;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 23px;

      span {
        color: var(--primary, #0f1538);
        font-family: Inter;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 23px;
      }
    }
  }

  .essential-importer-notice {
    background: #fffbdb;
    border-radius: 4px;
    display: flex;

    span {
      font-family: "Inter", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      display: flex;
      align-items: center;
      color: #b99518;
      padding: 20px;
      width: 80%;
    }

    svg {
      padding-left: 20px;
      padding-top: 23px;
      width: 23px;
    }
  }

  .essential-importer-radio-wrapper {
    display: grid;
    grid-gap: 15px;

    span {
      font-family: "Inter", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #515676;
      padding-top: 15px;

      span {
        color: var(--primary, #0f1538);
        font-family: Inter;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
      }
    }

    label {
      display: flex;

      input[type="radio"] {
        display: none;
      }

      .essential-radio-icon {
        margin-right: 10px;
        margin-top: 2px;
      }

      .radio-content {
        h3 {
          display: inline-flex;
          gap: 5px;
          font-family: "Inter", sans-serif;
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 20px;
          color: #0f1538;
          margin: unset;

          &.process {
            color: #d0d5dd;
          }
        }

        p {
          margin-bottom: unset;
          margin-top: 2px;
          font-family: "Inter", sans-serif;
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: 20px;
          color: #515676;
        }
      }
    }
  }
}
